document.addEventListener("DOMContentLoaded", () => {
	//? Includes
	//* // Variables
// Iphones
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

const body = document.querySelector('.body');
const burgerBtn = document.querySelector('.burger');
const nav = document.querySelector('.nav');
const mediaMobile = window.matchMedia("(max-width: 767px)");
const header = document.querySelector('.header');
const hero = document.querySelector('.hero');;
	//* // scrollLockIOS

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
};
	//* // Burger Menu
let menuIsOpen = false;
if (mediaMobile.matches) {
	nav.setAttribute('aria-hidden', true);
}

const burgerMenuOpen = () => {
	burgerBtn.classList.add('burger--active');
	body.classList.add('lock');
	nav.classList.add('nav--active')
	burgerBtn.setAttribute('aria-expanded', true);
	nav.setAttribute('aria-hidden', false);
	if (isiPad || isiPhone || isiPod) {
		disableScroll();
	}
	menuIsOpen = true;
}

const burgerMenuClose = () => {
	burgerBtn.classList.remove('burger--active');
	body.classList.remove('lock');
	nav.classList.remove('nav--active')
	burgerBtn.setAttribute('aria-expanded', false);
	nav.setAttribute('aria-hidden', true);
	if (isiPad || isiPhone || isiPod) {
		enableScroll();
	}
	menuIsOpen = false;
}

const burgerMenuSwitch = () => {
	switch (menuIsOpen) {
		case false:
			burgerMenuOpen();
			break;
		case true:
			burgerMenuClose();
			break;
	}
}

burgerBtn.addEventListener('click', () => {
	burgerMenuSwitch();
});
	//* // Header Scroll
window.addEventListener('scroll', () => {
	if (window.scrollY > 0) {
		header.classList.add('header--fixed');
	} else {
		header.classList.remove('header--fixed');
	}
});;
	//? -- //
});